import React from 'react';
import SearchForm from './SearchForm';
import { motion } from "framer-motion"

const HeaderPages = () => {

    const initial = {
        y: -20,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    return (
        <div className='header-pages'>
            <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="content-header-pages">
                <SearchForm />
            </motion.div>
        </div>
    );
};

export default HeaderPages;