import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { NavLink } from 'react-router-dom';

const Card = ({ meal }) => {

    const [mealsCardData, setMealsCardData] = useState([]);
    const [isFavorite, setIsFavorite] = useState(false);

    useEffect(() => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/lookup.php?i=" + meal.idMeal)
            .then((res) => setMealsCardData(res.data.meals));
    }, []);


    useEffect(() => {
        checkIfRecipeIsFavorite();
    }, [mealsCardData]);


    const checkIfRecipeIsFavorite = () => {

        const meal = mealsCardData[0];

        if (meal) {
            const { idMeal } = meal;

            if (typeof Storage !== "undefined") {
                const existingFavorites = JSON.parse(localStorage.getItem("EasyCookingFavoriteRecipe")) || [];

                // Vrifier si la recette est dj dans les favoris
                if (existingFavorites.some((favorite) => favorite.idMeal === idMeal)) {
                    //console.log("Cette recette est dj dans vos favoris.");
                    setIsFavorite(true);
                }
            } else {
                //console.log("Dsol, votre navigateur ne supporte pas le Web Storage.");
                setIsFavorite(false);
            }
        } else {
            //console.log("Cette recette n'est pas dans vos favoris.");
            setIsFavorite(false);
        }
    };

    return (
        <NavLink to={`/recipe/${meal.idMeal}`}>
            {
                mealsCardData
                    .map((cardMeal) =>
                        <div key={cardMeal.idMeal} className='card d-flex flex-v ai-c g10'>

                            {
                                isFavorite ? (
                                    <div className="btn-favoris-active"><i className="bi bi-heart-fill"></i></div>
                                ) : (
                                    <div className="btn-favoris"><i className="bi bi-heart"></i></div>
                                )
                            }

                            <LazyLoadImage className='img-card' key={cardMeal.idMeal} width="100%" height="320" src={cardMeal.strMealThumb} effect='blur' alt={'photo de ' + cardMeal.strMeal} />

                            <div className="infos-recipe d-flex flex-v jc-fe g3">


                                <div className='title'>{cardMeal.strMeal}</div>
                                <div className="star d-flex ai-c">
                                    <i className="bi bi-star"></i>
                                    <i className="bi bi-star"></i>
                                    <i className="bi bi-star"></i>
                                    <i className="bi bi-star"></i>
                                    <i className="bi bi-star"></i>
                                </div>

                                <div className="infos d-flex flex-v-m ai-fs-m ai-c g10 g5m">
                                    <span><i className="bi bi-geo-alt-fill"></i> {cardMeal.strArea}</span>
                                    <span><i className="bi bi-list-ul"></i> {cardMeal.strCategory}</span>
                                </div>
                            </div>

                            <div className="overlay-play">
                                <i className="bi bi-play-circle"></i>
                            </div>

                        </div>
                    )
            }
        </NavLink>
    );
};

export default Card;