import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import us from "../assets/img/us.png"
import gb from "../assets/img/gb.png"
import ca from "../assets/img/ca.png"
import fr from "../assets/img/fr.png"
import jm from "../assets/img/jm.png"
import cn from "../assets/img/cn.png"
import nl from "../assets/img/nl.png"
import eg from "../assets/img/eg.png"
import gr from "../assets/img/gr.png"
import ind from "../assets/img/in.png"
import ie from "../assets/img/ie.png"
import it from "../assets/img/it.png"
import jp from "../assets/img/jp.png"
import kn from "../assets/img/kn.png"
import my from "../assets/img/my.png"
import mx from "../assets/img/mx.png"
import ma from "../assets/img/ma.png"
import hr from "../assets/img/hr.png"
import no from "../assets/img/no.png"
import pt from "../assets/img/pt.png"
import ru from "../assets/img/ru.png"
import ar from "../assets/img/ar.png"
import es from "../assets/img/es.png"
import th from "../assets/img/th.png"
import vn from "../assets/img/vn.png"
import tr from "../assets/img/tr.png"
import tn from "../assets/img/tn.png"
import pl from "../assets/img/pl.png"
import ph from "../assets/img/ph.png"
import unk from "../assets/img/unk.png"
import LoaderComponants from './LoaderComponants';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { motion } from "framer-motion"


const ListByCountry = () => {
    const [isLoading, setIsLoading] = useState(true);
    const [mealsCountryData, setMealsCountryData] = useState([]);
    const [flags, setFlags] = useState({});
    const [numberDisplayCountry, setNumberDisplayCountry] = useState(false);

    const currentURL = window.location.href;
    const isHomePage = currentURL.endsWith('/');

    const displayBtnAllCountry = isHomePage ? '' : 'hide';

    useEffect(() => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/list.php?a=list")
            .then((res) => {
                setMealsCountryData(res.data.meals);
                setIsLoading(false);

                // Vrification des drapeaux ici au montage du composant
                const countries = res.data.meals.map((meal) => meal.strArea);
                const flagsData = {};

                countries.forEach((country) => {
                    if (country === 'American') { flagsData[country] = us; }
                    else if (country === 'British') { flagsData[country] = gb; }
                    else if (country === 'Canadian') { flagsData[country] = ca; }
                    else if (country === 'French') { flagsData[country] = fr; }
                    else if (country === 'Jamaican') { flagsData[country] = jm; }
                    else if (country === 'Chinese') { flagsData[country] = cn; }
                    else if (country === 'Dutch') { flagsData[country] = nl; }
                    else if (country === 'Egyptian') { flagsData[country] = eg; }
                    else if (country === 'Greek') { flagsData[country] = gr; }
                    else if (country === 'Indian') { flagsData[country] = ind; }
                    else if (country === 'Irish') { flagsData[country] = ie; }
                    else if (country === 'Italian') { flagsData[country] = it; }
                    else if (country === 'Japanese') { flagsData[country] = jp; }
                    else if (country === 'Kenyan') { flagsData[country] = kn; }
                    else if (country === 'Malaysian') { flagsData[country] = my; }
                    else if (country === 'Mexican') { flagsData[country] = mx; }
                    else if (country === 'Moroccan') { flagsData[country] = ma; }
                    else if (country === 'Croatian') { flagsData[country] = hr; }
                    else if (country === 'Norwegian') { flagsData[country] = no; }
                    else if (country === 'Portuguese') { flagsData[country] = pt; }
                    else if (country === 'Russian') { flagsData[country] = ru; }
                    else if (country === 'Argentinian') { flagsData[country] = ar; }
                    else if (country === 'Spanish') { flagsData[country] = es; }
                    else if (country === 'Thai') { flagsData[country] = th; }
                    else if (country === 'Vietnamese') { flagsData[country] = vn; }
                    else if (country === 'Turkish') { flagsData[country] = tr; }
                    else if (country === 'Tunisian') { flagsData[country] = tn; }
                    else if (country === 'Polish') { flagsData[country] = pl; }
                    else if (country === 'Filipino') { flagsData[country] = ph; }
                    else { flagsData[country] = unk; }
                });

                setFlags(flagsData);
            })
            .catch((error) => {
                console.error("Error fetching data:", error);
                setIsLoading(false);
            });
    }, []);

    const handleNavLinkClick = () => {
        window.scrollTo(0, 0);
    }

    useEffect(() => {
        if (window.innerWidth < 1200) {
            setNumberDisplayCountry(true);
        } else {
            setNumberDisplayCountry(false);
        }
    }, []);
    const screenMobile = isHomePage && numberDisplayCountry ? '12' : '50';

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }


    return (
        <div className="list-by-country c100">

            {
                isLoading ? (
                    <LoaderComponants />
                ) : (

                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}>
                        <div className="c100m d-flex flex-v-m g10m ai-c jc-sb mb-20">
                            <div className='title'>Recipe by country</div>
                            <NavLink className={`btn btn-primary-border fs-14 ${displayBtnAllCountry}`} to="/search-by-country">
                                See all country <i className="bi bi-arrow-right-circle fs-18"></i>
                            </NavLink>
                        </div>

                        <div className='list-by-country-flag d-flex jc-sb wrap'>
                            {mealsCountryData && mealsCountryData
                                .slice(0, screenMobile)
                                .map((mealsCountry) => {

                                    const flag = flags[mealsCountry.strArea];

                                    return (
                                        <NavLink
                                            key={mealsCountry.strArea}
                                            to={`/search-by-country/${mealsCountry.strArea}`}
                                            onClick={handleNavLinkClick}>
                                            <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className='box-flag d-flex flex-v ai-c g10'>
                                                {flag &&
                                                    <LazyLoadImage key={mealsCountry.idMeal} width="50" height="50" src={flag} effect='blur' alt={'photo de ' + mealsCountry.strArea} />
                                                }
                                                <span>{mealsCountry.strArea}</span>
                                            </motion.div>
                                        </NavLink>
                                    );
                                })}
                        </div>
                    </motion.div>

                )
            }

        </div>
    );
};

export default ListByCountry;
