import React, { useEffect } from 'react';

const OverlaySearch = () => {

    useEffect(() => {

        var overlaySearchForm = document.querySelector(".overlay-search-form");
        var inputSearch = document.querySelector("#inputSearch");
        var searchForm = document.querySelector(".content-header");

        inputSearch.addEventListener("focus", () => {
            //searchForm.style.position = 'relative';
            //searchForm.style.zIndex = '21';
            overlaySearchForm.style.zIndex = '20';
            overlaySearchForm.classList.remove("hide");
            overlaySearchForm.classList.replace("bgOut", "bgIn");
        });

        inputSearch.addEventListener("blur", () => {
            //searchForm.style.position = '';
            //searchForm.style.zIndex = '';
            overlaySearchForm.classList.replace("bgIn", "bgOut");
            setTimeout(() => {
                overlaySearchForm.classList.add("hide");
            }, 300);
        });

    }, []);

    return (
        <div className="overlay-search-form bgOut hide"></div>
    );
};

export default OverlaySearch;