import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { NavLink } from 'react-router-dom';
import LoaderComponants from './LoaderComponants';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Beef from "../assets/img/beef.jpg";
import Breakfast from "../assets/img/breakfast.jpg";
import Chicken from "../assets/img/chicken.jpg";
import Dessert from "../assets/img/dessert.jpg";
import Goat from "../assets/img/goat.jpg";
import Lamb from "../assets/img/lamb.jpg";
import Pasta from "../assets/img/pasta.jpg";
import Pork from "../assets/img/pork.jpg";
import Seafood from "../assets/img/seafood.jpg";
import Side from "../assets/img/side.jpg";
import Starter from "../assets/img/starter.jpg";
import Vegan from "../assets/img/vegan.jpg";
import Vegetarian from "../assets/img/vegetarian.jpg";
import unk from "../assets/img/vegetarian.jpg";
import { motion } from "framer-motion"

const ListByCategory = () => {

    const [isLoading, setIsLoading] = useState(true);
    const [mealsCategorieData, setMealsCategorieData] = useState([]);
    const [recipeCounts, setRecipeCounts] = useState({});

    const currentURL = window.location.href;
    const isHomePage = currentURL.endsWith('/');

    const numberDisplayCategory = isHomePage ? '10' : '1000';
    const displayBtnAllCategory = isHomePage ? '' : 'hide';

    const [imgCategory, setImgCategory] = useState({});

    useEffect(() => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/list.php?c=list")
            .then((res) => {
                setMealsCategorieData(res.data.meals);
                setIsLoading(false);

                // Vrification des drapeaux ici au montage du composant
                const bgCategory = res.data.meals.map((meal) => meal.strCategory);
                const imgCategoryData = {};

                bgCategory.forEach((country) => {
                    if (country === 'Beef') { imgCategoryData[country] = Beef; }
                    else if (country === 'Breakfast') { imgCategoryData[country] = Breakfast; }
                    else if (country === 'Chicken') { imgCategoryData[country] = Chicken; }
                    else if (country === 'Dessert') { imgCategoryData[country] = Dessert; }
                    else if (country === 'Goat') { imgCategoryData[country] = Goat; }
                    else if (country === 'Lamb') { imgCategoryData[country] = Lamb; }
                    else if (country === 'Pasta') { imgCategoryData[country] = Pasta; }
                    else if (country === 'Pork') { imgCategoryData[country] = Pork; }
                    else if (country === 'Seafood') { imgCategoryData[country] = Seafood; }
                    else if (country === 'Side') { imgCategoryData[country] = Side; }
                    else if (country === 'Starter') { imgCategoryData[country] = Starter; }
                    else if (country === 'Vegan') { imgCategoryData[country] = Vegan; }
                    else if (country === 'Vegetarian') { imgCategoryData[country] = Vegetarian; }
                    else { imgCategoryData[country] = unk; }
                });

                setImgCategory(imgCategoryData);


            })
            .catch((error) => {
                console.error("Error fetching data:", error);
                setIsLoading(false);
            });
    }, []);

    useEffect(() => {
        // Fonction pour obtenir le nombre de recettes par catgorie
        const fetchRecipeCounts = async () => {
            const categoryCounts = {};

            try {
                for (const category of mealsCategorieData) {
                    const response = await axios.get(`https://www.themealdb.com/api/json/v1/1/filter.php?c=${category.strCategory}`);
                    const count = response.data.meals ? response.data.meals.length : 0;
                    categoryCounts[category.strCategory] = count;
                }
                setRecipeCounts(categoryCounts);
            } catch (error) {
                console.error(error);
            }
        };

        fetchRecipeCounts();
    }, [mealsCategorieData]);

    const handleNavLinkClick = () => {
        window.scrollTo(0, 0);
    }

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    return (
        <div className="list-by-category c100">

            {
                isLoading ? (
                    <LoaderComponants />
                ) : (

                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}>

                        <div className="c100m d-flex flex-v-m g10m ai-c jc-sb mb-20">
                            <div className='title'>Recipe by category</div>
                            <NavLink className={`btn btn-primary-border fs-14 w-fc ${displayBtnAllCategory}`} to="/search-by-categorie">
                                See all categories <i className="bi bi-arrow-right-circle fs-18"></i>
                            </NavLink>
                        </div >

                        <div className='container d-flex g30 g10m wrap'>
                            {mealsCategorieData && mealsCategorieData
                                .sort((a, b) => a.strCategory - b.strCategory)
                                .slice(0, numberDisplayCategory)
                                .map((mealsCategorie) => (
                                    <NavLink
                                        key={mealsCategorie.strCategory}
                                        to={`/search-by-categorie/${mealsCategorie.strCategory}`}
                                        onClick={handleNavLinkClick}
                                    >
                                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className='box-list-by-category'>
                                            <LazyLoadImage key={mealsCategorie.strCategory} width="100%" height="300" src={imgCategory[mealsCategorie.strCategory]} effect='blur' alt={'photo de ' + mealsCategorie.strCategory} />
                                            <div className="infos d-flex flex-v ai-c">
                                                <div className='title'>{mealsCategorie.strCategory}</div>
                                                <div className='count-recipe'>Total recipes : {recipeCounts[mealsCategorie.strCategory]}</div>
                                            </div>
                                        </motion.div>
                                    </NavLink>
                                ))}
                        </div>

                    </motion.div>

                )
            }

        </div >
    );
};

export default ListByCategory;
