import axios from 'axios';
import React, { useEffect, useState } from 'react';
import Card from './Card';
import LoaderComponants from './LoaderComponants';
import { motion } from "framer-motion"

const RecipeRandom = () => {

    const [isLoading, setIsLoading] = useState(true);
    const [recipeRandomData, setRecipeRandomData] = useState([]);
    const [numberDisplayRecipe, setNumberDisplayRecipe] = useState(false);

    useEffect(() => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/search.php?s=")
            .then((res) => {
                setRecipeRandomData(res.data.meals);
                setIsLoading(false);
            })
            .catch((error) => {
                console.error("Error fetching data:", error);
                setIsLoading(false);
            });
    }, []);

    // Fonction pour mlanger un tableau de manire alatoire
    const shuffleArray = (array) => {
        for (let i = array.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
    };

    useEffect(() => {
        if (window.innerWidth < 1200) {
            setNumberDisplayRecipe(true);
        } else {
            setNumberDisplayRecipe(false);
        }
    }, []);
    const screenMobile = numberDisplayRecipe ? '4' : '5';

    return (
        <div className='recipe-random'>

            {
                isLoading ? (
                    <LoaderComponants />
                ) :

                    <motion.div initial={{ y: 50, opacity: 0 }} animate={{ y: 0, opacity: 1 }} transition={{ duration: 0.3 }}>

                        <div className='title'>Recipe of the day <i className="bi bi-heart-fill fc-red"></i></div>
                        <div className='d-flex g30 g0m mt-20 wrap'>

                            {recipeRandomData && shuffleArray(recipeRandomData)
                                .slice(0, screenMobile)
                                .map((meal) => <Card key={meal.idMeal} meal={meal} />)
                            }

                        </div>

                    </motion.div>

            }
        </div >
    );
};

export default RecipeRandom;
