import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import ListByCountry from '../components/ListByCountry';
import TopBar from '../components/TopBar';
import Card from '../components/Card';
import HeaderPages from '../components/HeaderPages';
import OverlaySearch from '../components/OverlaySearch';
import Footer from '../components/Footer';
import ListBlog from '../components/ListBlog';
import Copyright from '../components/Copyright';
import { motion } from "framer-motion"

const SearchByCountry = () => {
    const location = useLocation();
    const cleanCountry = location.pathname.split('/').pop();
    const [mealsCountryData, setMealsCountryData] = useState([]);
    const [displayErrorMessage, setDisplayErrorMessage] = useState(false);

    const fetchData = () => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/filter.php?a=" + cleanCountry)
            .then((res) => {
                if (res.data.meals !== null) {
                    setMealsCountryData(res.data.meals);
                } else {
                    setMealsCountryData([]); // Dfinir comme un tableau vide si pas de rsultats
                }
            })
            .catch((error) => {
                console.error("Erreur lors de la rcupration des donnes :", error);
                setMealsCountryData([]); // Dfinir comme un tableau vide en cas d'erreur
            });
    };

    useEffect(() => {
        setTimeout(() => {
            fetchData();
        }, 50);
    }, [cleanCountry]);

    useEffect(() => {
        if (cleanCountry === 'search-by-country') {
            setDisplayErrorMessage(true);
        } else {
            setDisplayErrorMessage(false);
        }
    }, []);

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    const initialPage = {
        opacity: 0
    }
    const whileInViewPage = {
        opacity: 1
    }
    const transitionPage = {
        duration: 0.5
    }
    const viewportPage = {
        once: true
    }

    return (
        <motion.div initial={initialPage} whileInView={whileInViewPage} transition={transitionPage} viewport={viewportPage} className="main">
            <OverlaySearch />
            <TopBar />
            <HeaderPages />
            <div className="page-content-list-country">
                <div className="d-flex flex-v g50 mt-50">

                    {/* <div className="d-flex g30 wrap mt-20"> */}
                    {mealsCountryData.length === 0 ? (

                        <div className={`c100 p30 bg-black10 br20 d-flex flex-v ai-c g20 ${displayErrorMessage ? 'hide' : ''}`}>
                            <span>No results correspond to "{cleanCountry}".</span>
                        </div>

                    ) : (

                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="d-flex flex-v g30">

                            <div className="c100 d-flex jc-fs jc-c-m">
                                <div className='fs-28 fs-22m fw-600'>All recipes from {cleanCountry}</div>
                            </div>

                            <div className="d-flex g30 g0m wrap">
                                {mealsCountryData.map((meal) => <Card key={meal.idMeal} meal={meal} />)}
                            </div>
                        </motion.div>

                    )}
                    {/* </div> */}

                    <ListByCountry />
                    <ListBlog />
                </div>
            </div>
            <Footer />
            <Copyright />
        </motion.div>
    );
};

export default SearchByCountry;
