import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import article1 from "../assets/img/article1.jpg";
import article2 from "../assets/img/article2.jpg";
import article3 from "../assets/img/article3.jpg";
import article4 from "../assets/img/article4.jpg";
import LoaderComponants from './LoaderComponants';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { motion } from "framer-motion"

const ListBlog = () => {

    const [isLoading, setIsLoading] = useState(true);
    const currentURL = window.location.href;
    const isHomePage = currentURL.endsWith('/');
    const isBlogPage = currentURL.endsWith('/blog');

    const displayBtnAllArticles = isHomePage ? '' : 'hide';
    const displayLimitArticles = isBlogPage ? '' : 'hide';

    const [setupDisplay, setSetupDisplay] = useState(false);
    const [setupDisplayHeightImg, setSetupDisplayHeightImg] = useState(false);
    const [setupDisplayWidthImg, setSetupDisplayWidthImg] = useState(false);
    const [btnDisplayArticle, setBtnDisplayArticle] = useState(false);

    const changeSetupDisplyToColomn = () => {
        setSetupDisplay(false);
        setSetupDisplayHeightImg(false);
        setSetupDisplayWidthImg(false);
    }

    const changeSetupDisplyToList = () => {
        setSetupDisplay(true);
        setSetupDisplayHeightImg(true);
        setSetupDisplayWidthImg(true);
    }

    useEffect(() => {
        setIsLoading(false);
    }, []);

    useEffect(() => {
        if (window.innerWidth < 1200) {
            setBtnDisplayArticle(true);
        } else {
            setBtnDisplayArticle(false);
        }
    }, []);

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }


    return (
        <div className='list-blog'>

            {
                isLoading ? (
                    <LoaderComponants />
                ) : (
                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}>

                        <div className="c100m d-flex flex-v-m g10m ai-c jc-sb mb-20">
                            <div className='title'>Blog</div>

                            <div className="d-flex ai-c g30">



                                <div className={`btn-display d-flex ai-c g10 ${btnDisplayArticle ? 'hide' : ''}`}>
                                    <i className="bi bi-grid-fill fs-24" onClick={changeSetupDisplyToColomn}></i>
                                    <i className="bi bi-list fs-24" onClick={changeSetupDisplyToList}></i>
                                </div>

                                <NavLink className={`btn btn-primary-border fs-14 ${displayBtnAllArticles}`} to="/blog">
                                    See all articles <i className="bi bi-arrow-right-circle fs-18"></i>
                                </NavLink>
                            </div>

                        </div>

                        <div className="c100 d-flex flex-v g20">

                            <div className={`c100 d-flex g20 ${setupDisplay ? 'flex-v' : ''} ${btnDisplayArticle ? 'flex-v' : ''}`}>

                                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}
                                    className="card-article c100 d-flex flex-v-m ai-c" style={{ height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                    <div className="card-img c100m" style={{ width: setupDisplayWidthImg ? '50%' : '50%', height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                        <LazyLoadImage width="100%" height="100%" src={article1} effect='blur' alt={'photo de ' + article1} />
                                    </div>
                                    <div className="content-card-article c50 c100m d-flex flex-v g20">
                                        <div className="c100 d-flex flex-v flex-h-m ai-c-m g5 g10m">
                                            <div className="title">Title article 1</div>
                                            <div className="date">2022.06.21</div>
                                        </div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ex cumque soluta, doloremque libero ea commodi dicta eius quis impedit...
                                        </div>
                                        <NavLink to="/blog/article/">
                                            <div className="btn btn-primary w-fc">Read</div>
                                        </NavLink>
                                    </div>
                                </motion.div>

                                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}
                                    className="card-article c100 d-flex flex-v-m ai-c" style={{ height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                    <div className="card-img c100m" style={{ width: setupDisplayWidthImg ? '50%' : '50%', height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                        <LazyLoadImage width="100%" height="100%" src={article2} effect='blur' alt={'photo de ' + article2} />
                                    </div>
                                    <div className="content-card-article c50 c100m d-flex flex-v g20">
                                        <div className="c100 d-flex flex-v flex-h-m ai-c-m g5 g10m">
                                            <div className="title">Title article 2</div>
                                            <div className="date">2022.06.21</div>
                                        </div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ex cumque soluta, doloremque libero ea commodi dicta eius quis impedit...
                                        </div>
                                        <NavLink to="/blog/article/">
                                            <div className="btn btn-primary w-fc">Read</div>
                                        </NavLink>
                                    </div>
                                </motion.div>

                            </div>

                            <div className={`c100 d-flex g20 ${setupDisplay ? 'flex-v' : ''} ${btnDisplayArticle ? 'flex-v' : ''}`}>

                                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}
                                    className="card-article c100 d-flex flex-v-m ai-c" style={{ height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                    <div className="card-img c100m" style={{ width: setupDisplayWidthImg ? '50%' : '50%', height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                        <LazyLoadImage width="100%" height="100%" src={article3} effect='blur' alt={'photo de ' + article3} />
                                    </div>
                                    <div className="content-card-article c50 c100m d-flex flex-v g20">
                                        <div className="c100 d-flex flex-v flex-h-m ai-c-m g5 g10m">
                                            <div className="title">Title article 3</div>
                                            <div className="date">2022.06.21</div>
                                        </div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ex cumque soluta, doloremque libero ea commodi dicta eius quis impedit...
                                        </div>
                                        <NavLink to="/blog/article/">
                                            <div className="btn btn-primary w-fc">Read</div>
                                        </NavLink>
                                    </div>
                                </motion.div>

                                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}
                                    className="card-article c100 d-flex flex-v-m ai-c" style={{ height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                    <div className="card-img c100m" style={{ width: setupDisplayWidthImg ? '50%' : '50%', height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                        <LazyLoadImage width="100%" height="100%" src={article4} effect='blur' alt={'photo de ' + article4} />
                                    </div>
                                    <div className="content-card-article c50 c100m d-flex flex-v g20">
                                        <div className="c100 d-flex flex-v flex-h-m ai-c-m g5 g10m">
                                            <div className="title">Title article 4</div>
                                            <div className="date">2022.06.21</div>
                                        </div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ex cumque soluta, doloremque libero ea commodi dicta eius quis impedit...
                                        </div>
                                        <NavLink to="/blog/article/">
                                            <div className="btn btn-primary w-fc">Read</div>
                                        </NavLink>
                                    </div>
                                </motion.div>

                            </div>

                            <div className={`c100 d-flex g20 ${displayLimitArticles} ${setupDisplay ? 'flex-v' : ''} ${btnDisplayArticle ? 'flex-v' : ''}`}>

                                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}
                                    className="card-article c100 d-flex flex-v-m ai-c" style={{ height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                    <div className="card-img c100m" style={{ width: setupDisplayWidthImg ? '50%' : '50%', height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                        <LazyLoadImage width="100%" height="100%" src={article2} effect='blur' alt={'photo de ' + article2} />
                                    </div>
                                    <div className="content-card-article c50 c100m d-flex flex-v g20">
                                        <div className="c100 d-flex flex-v flex-h-m ai-c-m g5 g10m">
                                            <div className="title">Title article 5</div>
                                            <div className="date">2022.06.21</div>
                                        </div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ex cumque soluta, doloremque libero ea commodi dicta eius quis impedit...
                                        </div>
                                        <NavLink to="/blog/article/">
                                            <div className="btn btn-primary w-fc">Read</div>
                                        </NavLink>
                                    </div>
                                </motion.div>

                                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}
                                    className="card-article c100 d-flex flex-v-m ai-c" style={{ height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                    <div className="card-img c100m" style={{ width: setupDisplayWidthImg ? '50%' : '50%', height: setupDisplayHeightImg ? '250px' : '300px' }}>
                                        <LazyLoadImage width="100%" height="100%" src={article1} effect='blur' alt={'photo de ' + article1} />
                                    </div>
                                    <div className="content-card-article c50 c100m d-flex flex-v g20">
                                        <div className="c100 d-flex flex-v flex-h-m ai-c-m g5 g10m">
                                            <div className="title">Title article 6</div>
                                            <div className="date">2022.06.21</div>
                                        </div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptas ex cumque soluta, doloremque libero ea commodi dicta eius quis impedit...
                                        </div>
                                        <NavLink to="/blog/article/">
                                            <div className="btn btn-primary w-fc">Read</div>
                                        </NavLink>
                                    </div>
                                </motion.div>

                            </div>

                        </div>

                    </motion.div>
                )
            }

        </div >
    );
};

export default ListBlog;