import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';

const Navigation = () => {

    useEffect(() => {

        const toggleMenuDrop = document.querySelector(".no-button");
        const menuDrop = document.querySelector(".menu-drop");
        toggleMenuDrop.addEventListener("click", () => {
            menuDrop.classList.remove("hide");
        });
        menuDrop.addEventListener("mouseleave", () => {
            menuDrop.classList.add("hide");
        });

    }, []);

    return (
        <div className="navigation">
            <ul>
                <NavLink to="/" className={(nav) => (nav.isActive ? "nav-active" : "")}>
                    <li>Home</li>
                </NavLink>

                <li className='no-button'>
                    <i className="bi bi-search"></i> Find your recipe
                    <div className="menu-drop hide">
                        <ul>
                            <NavLink to="/search-by-ingredient" className={(nav) => (nav.isActive ? "nav-active-drop" : "")}>
                                <li>Search by ingredient</li>
                            </NavLink>
                            <NavLink to="/search-by-categorie" className={(nav) => (nav.isActive ? "nav-active-drop" : "")}>
                                <li>Search by category</li>
                            </NavLink>
                            <NavLink to="/search-by-country" className={(nav) => (nav.isActive ? "nav-active-drop" : "")}>
                                <li>Search by country</li>
                            </NavLink>
                        </ul>
                    </div>
                </li>

                <NavLink to="/blog" className={(nav) => (nav.isActive ? "nav-active" : "")}>
                    <li>Blog</li>
                </NavLink>
                <NavLink to="/about" className={(nav) => (nav.isActive ? "nav-active" : "")}>
                    <li>About</li>
                </NavLink>
            </ul>
        </div>
    );
};

export default Navigation;