import React, { useEffect, useState } from 'react';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { NavLink } from 'react-router-dom';

function limitCaractere(chaine, limite) {
    if (chaine.length <= limite) {
        return chaine;
    } else {
        return chaine.substring(0, limite) + '...';
    }
}

const CardResult = ({ meal }) => {
    const titleMeal = meal.strMeal;
    const mealsCardResultData = meal.idMeal;
    const titleMealLimited = limitCaractere(titleMeal, 40);
    const [isFavorite, setIsFavorite] = useState(false);

    //const currentURL = window.location.href;
    //const isRecipePage = currentURL.includes('/recipe/');

    useEffect(() => {
        checkIfRecipeIsFavorite();
    }, [mealsCardResultData]);

    const checkIfRecipeIsFavorite = () => {
        if (mealsCardResultData) {
            if (typeof Storage !== "undefined") {
                const existingFavorites = JSON.parse(localStorage.getItem("EasyCookingFavoriteRecipe")) || [];
                setIsFavorite(false);

                // Vrifier si la recette est dj dans les favoris
                if (existingFavorites.some((favorite) => favorite.idMeal === mealsCardResultData)) {
                    setIsFavorite(true);
                } else {
                    setIsFavorite(false);
                }
            } else {
                setIsFavorite(false);
            }
        } else {
            setIsFavorite(false);
        }
    };

    return (
        <NavLink to={`/recipe/${meal.idMeal}`}>
            <div className="card-result d-flex ai-c jc-sb">
                <div className="d-flex ai-c g20">
                    {isFavorite ? (
                        <i className="bi bi-heart-fill fs-16 fc-red"></i>
                    ) : null}
                    <LazyLoadImage key={meal.idMeal} width="40" height="40" src={meal.strMealThumb} effect='blur' alt={'photo de ' + meal.strMeal} />
                    <div className="d-flex flex-v">
                        <div className="d-flex aic- g10">
                            <div className='title'>{titleMealLimited}</div>
                            <div className="star d-flex ai-c">
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                            </div>
                        </div>
                        <div className="tag d-flex ai-c g20">
                            <span><i className="bi bi-tag-fill"></i> {meal.strCategory}</span>
                            <span><i className="bi bi-geo-alt-fill"></i> {meal.strArea}</span>
                        </div>
                    </div>
                </div>
                <i className="bi bi-arrow-right-circle"></i>
            </div>
        </NavLink>
    );
};

export default CardResult;
