import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import Navigation from './Navigation';
import PopLogin from './PopLogin';
import BlocMenuMobile from './BlocMenuMobile';
import logo from "../assets/img/logo.png"
import { LazyLoadImage } from 'react-lazy-load-image-component';

const TopBar = () => {

    const currentURL = window.location.href;
    const isHomePage = currentURL.endsWith('/');
    const [isTopPage, setIsTopPage] = useState(true);

    const openPopLogin = () => {
        const boxLogin = document.querySelector(".box-login");
        const contentBoxLogin = document.querySelector(".content-box-login");
        boxLogin.classList.remove("hide");
        boxLogin.classList.replace("bgOut", "bgIn");
        setTimeout(() => {
            contentBoxLogin.classList.remove("hide");
            contentBoxLogin.classList.replace("contentOut", "contentIn");
        }, 300);
        closePopLogin();
    }

    const closePopLogin = () => {
        const boxLogin = document.querySelector(".box-login");
        const contentBoxLogin = document.querySelector(".content-box-login");
        boxLogin.addEventListener("click", (event) => {
            if (!contentBoxLogin.contains(event.target)) {
                boxLogin.classList.replace("bgIn", "bgOut");
                setTimeout(() => {
                    boxLogin.classList.add("hide");
                    contentBoxLogin.classList.add("hide");
                    contentBoxLogin.classList.replace("contentIn", "contentOut");
                }, 300);
            }
        });
    }

    useEffect(() => {
        const handleScroll = () => {
            setIsTopPage(window.scrollY === 0);
        }
        window.addEventListener("scroll", handleScroll);
    }, []);

    const openMenuMobile = () => {
        const menuMobile = document.querySelector(".menu-mobile");
        const contentMenuMobile = document.querySelector(".content-menu-mobile");
        menuMobile.style.width = '100%';
        setTimeout(() => {
            contentMenuMobile.classList.replace("bgOut", "bgIn");
        }, 150);
    }


    return (

        <div>
            <BlocMenuMobile />
            <PopLogin />
            <div className={`top-bar d-flex ai-c pos-f t0 l0 ${isTopPage ? '' : 'top-bar-active'}`}>
                <div className="c25">
                    <NavLink to="/">
                        <LazyLoadImage width="200" height="37.83" src={logo} effect='blur' alt={'Easy Cooking'} />
                    </NavLink>
                </div>
                <div className="c50">
                    <Navigation />
                </div>
                <div className="c25 d-flex jc-fe ai-c g30">
                    <NavLink to="/myfavoritelist">
                        <div className="btn btn-favorite-list"><i className="bi bi-journal-check"></i> My favorites list</div>
                    </NavLink>
                    <div className="btn btn-login" onClick={openPopLogin}><i className="bi bi-person-circle"></i> Login</div>
                </div>
            </div>


            <div className={`top-bar-mobile d-flex jc-sb ai-c pos-f t0 l0 ${isTopPage ? '' : 'top-bar-active'}`}>
                <div className="c20">
                    <i className="bi bi-list" onClick={openMenuMobile}></i>
                </div>
                <div className="60 ta-c">
                    <NavLink to="/">
                        <LazyLoadImage width="200" height="23.64" src={logo} effect='blur' alt={'Easy Cooking'} />
                    </NavLink>
                </div>
                <div className="c20 ta-r">
                    <i className="bi bi-person-circle" onClick={openPopLogin}></i>
                </div>
            </div>


        </div >
    );
};

export default TopBar;