import React, { useState, useEffect } from 'react';
import axios from 'axios';
import OverlaySearch from '../components/OverlaySearch';
import TopBar from '../components/TopBar';
import HeaderPages from '../components/HeaderPages';
import Card from '../components/Card';
import RecipeRandom from '../components/RecipeRandom';
import Footer from '../components/Footer';
import BlocNewsletterSub from '../components/BlocNewsletterSub';
import LoaderComponants from '../components/LoaderComponants';
import Copyright from '../components/Copyright';
import { motion } from "framer-motion"

const App = () => {
    const [isLoading, setIsLoading] = useState(true);
    const [mealsCategorieData, setMealsCategorieData] = useState([]);
    const [favoriteRecipes, setFavoriteRecipes] = useState([]);
    const [favoriteRecipesDetails, setFavoriteRecipesDetails] = useState([]);

    useEffect(() => {
        // Rcuprer les recettes par catgorie en utilisant l'API
        axios
            .get('https://www.themealdb.com/api/json/v1/1/list.php?c=list')
            .then((res) => {
                setMealsCategorieData(res.data.meals);
                setIsLoading(false);
            })
            .catch((error) => {
                console.log(error);
                setIsLoading(false);
            });

        // Charger les recettes favorites depuis le WebStorage
        const favorites = localStorage.getItem('EasyCookingFavoriteRecipe');
        if (favorites) {
            const favoriteData = JSON.parse(favorites);
            setFavoriteRecipes(favoriteData);
        }
    }, []);

    // Fonction pour charger les dtails des recettes favorites depuis l'API
    const loadFavoriteRecipesDetails = () => {
        const promises = favoriteRecipes.map((favorite) =>
            axios.get(`https://www.themealdb.com/api/json/v1/1/lookup.php?i=${favorite.idMeal}`)
        );

        Promise.all(promises)
            .then((responses) => {
                const favoriteRecipesData = responses.map((res, index) => {
                    const details = res.data.meals[0];
                    return {
                        idMeal: favoriteRecipes[index].idMeal,
                        recipeTitle: favoriteRecipes[index].recipeTitle,
                        ...details,
                    };
                });
                setFavoriteRecipesDetails(favoriteRecipesData);
            })
            .catch((error) => console.log(error));
    };

    useEffect(() => {
        if (favoriteRecipes.length > 0) {
            loadFavoriteRecipesDetails();
        }
    }, [favoriteRecipes]);

    // Fonction pour supprimer une recette des favoris
    const removeFromFavorites = (idMealToRemove) => {
        const updatedFavorites = favoriteRecipes.filter((favorite) => favorite.idMeal !== idMealToRemove);
        setFavoriteRecipes(updatedFavorites);
        localStorage.setItem('EasyCookingFavoriteRecipe', JSON.stringify(updatedFavorites));
        loadFavoriteRecipesDetails(); // Recharger les dtails des recettes favorites aprs la suppression
    };

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    const initialPage = {
        opacity: 0
    }
    const whileInViewPage = {
        opacity: 1
    }
    const transitionPage = {
        duration: 0.5
    }
    const viewportPage = {
        once: true
    }

    return (

        <motion.div initial={initialPage} whileInView={whileInViewPage} transition={transitionPage} viewport={viewportPage} className="main">
            <OverlaySearch />
            <TopBar />
            <HeaderPages />
            {
                isLoading ? (
                    <LoaderComponants />
                ) : (
                    <div>

                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="page-content-myfavorites d-flex flex-v g50">
                            <div className="title ta-c-m">My Favorite List</div>
                            <div className="c100 d-flex ai-c g30 wrap">

                                {favoriteRecipesDetails.length === 0 ? (

                                    <div className="no-recipe-yet d-flex flex-v ai-c g10">
                                        <i className="bi bi-inboxes"></i>
                                        <div className="title ta-c-m">No favorite recipe yet</div>
                                        <div className="sub-title ta-c-m">Why do you save your favorite recipes here?</div>
                                        <div className="description ta-c-m">
                                            To save your favorite dishes, click on the "add to favorites" icon at the top right of the recipe page.
                                        </div>
                                    </div>


                                ) : (

                                    favoriteRecipesDetails.map((recipe) => (
                                        <div key={recipe.idMeal} className='c100m'>
                                            <div className="bloc-card">
                                                <Card meal={recipe} />
                                            </div>
                                            <button className='delete-favorite' onClick={() => removeFromFavorites(recipe.idMeal)}>
                                                <i className="bi bi-x-lg"></i> Remove from favorites
                                            </button>
                                        </div>
                                    ))

                                )}

                            </div>
                            <RecipeRandom />
                            <BlocNewsletterSub />
                        </motion.div>

                    </div>
                )
            }
            <Footer />
            <Copyright />
        </motion.div>

    );
};

export default App;
