import React from 'react';
import TopBar from '../components/TopBar';
import HeaderPages from '../components/HeaderPages';
import OverlaySearch from '../components/OverlaySearch';
import Footer from '../components/Footer';
import Techno from "../assets/img/techno.jpg"
import mika from "../assets/img/mika.png"
import Copyright from '../components/Copyright';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { motion } from "framer-motion"

const About = () => {

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    const initialPage = {
        opacity: 0
    }
    const whileInViewPage = {
        opacity: 1
    }
    const transitionPage = {
        duration: 0.5
    }
    const viewportPage = {
        once: true
    }

    return (
        <motion.div initial={initialPage} whileInView={whileInViewPage} transition={transitionPage} viewport={viewportPage} className='main'>
            <OverlaySearch />
            <TopBar />
            <HeaderPages />
            <div className="page-content-about d-flex flex-v g50">

                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="c100 d-flex jc-c">
                    <div className="title">About this application</div>
                </motion.div>

                <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="advertisement error-message d-flex flex-v-m ai-c g20 g0m">
                    <i className="bi bi-exclamation-triangle-fill"></i>
                    <div className="d-flex flex-v g5">
                        <div className="title ta-c-m">This application is not in production</div>
                        <div className="description ta-c-m">This application is a model developed to show my skills in the context of a search for a development mission. It is not referenced on Google or other social media sites.</div>
                    </div>
                </motion.div>

                <div className="c100 d-flex flex-v g30 g0m">

                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="c100 d-flex flex-v-m ai-c g30 g0m">
                        <div className="c50 c100m">
                            <LazyLoadImage width="100%" height="300" src={Techno} effect='blur' alt={'Technologie'} />
                        </div>
                        <div className="c50 c100m d-flex flex-v g20">
                            <div className="sub-title ta-c-m">Technology used</div>
                            <div className="text-content ta-c-m">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Laudantium architecto vitae quibusdam commodi id nulla at culpa eveniet adipisci, quod repellat. Dolores repellat veritatis asperiores nobis officia, praesentium labore a ea quis necessitatibus? Neque, accusantium, est, obcaecati temporibus iusto exercitationem omnis ipsa itaque error numquam eligendi autem a corrupti accusamus?</div>
                        </div>
                    </motion.div>

                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="c100 d-flex flex-v-m flex-hi ai-c g30 g0m">
                        <div className="c50 c100m ta-c-m">
                            <LazyLoadImage className='img-mika' width="100%" height="300" src={mika} effect='blur' alt={'Technologie'} />
                        </div>
                        <div className="c50 c100m d-flex flex-v g20">
                            <div className="sub-title ta-c-m">Technology used</div>
                            <div className="text-content ta-c-m">Lorem ipsum dolor sit, amet consectetur adipisicing elit. Laudantium architecto vitae quibusdam commodi id nulla at culpa eveniet adipisci, quod repellat. Dolores repellat veritatis asperiores nobis officia, praesentium labore a ea quis necessitatibus? Neque, accusantium, est, obcaecati temporibus iusto exercitationem omnis ipsa itaque error numquam eligendi autem a corrupti accusamus?</div>
                        </div>
                    </motion.div>

                </div>

            </div>
            <Footer />
            <Copyright />
        </motion.div>
    );
};

export default About;