import React, { useEffect, useState } from 'react';
import OverlaySearch from '../components/OverlaySearch';
import TopBar from '../components/TopBar';
import HeaderPages from '../components/HeaderPages';
import Footer from '../components/Footer';
import BlocNewsletterSub from '../components/BlocNewsletterSub';
import article1 from "../assets/img/article1.jpg";
import article2 from "../assets/img/article2.jpg";
import article3 from "../assets/img/article3.jpg";
import article4 from "../assets/img/article4.jpg";
import PopShare from '../components/PopShare';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import LoaderComponants from '../components/LoaderComponants';
import Copyright from '../components/Copyright';
import { motion } from "framer-motion"

const Articles = () => {

    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        setIsLoading(false);
    }, []);

    const openPopShare = () => {
        const boxShare = document.querySelector(".box-share");
        const contentBoxShare = document.querySelector(".content-box-share");
        boxShare.classList.remove("hide");
        boxShare.classList.replace("bgOut", "bgIn");
        setTimeout(() => {
            contentBoxShare.classList.remove("hide");
            contentBoxShare.classList.replace("contentOut", "contentIn");
        }, 300);
        closePopShare();
    }

    const closePopShare = () => {
        const boxShare = document.querySelector(".box-share");
        const contentBoxShare = document.querySelector(".content-box-share");
        boxShare.addEventListener("click", (event) => {
            if (!contentBoxShare.contains(event.target)) {
                boxShare.classList.replace("bgIn", "bgOut");
                setTimeout(() => {
                    boxShare.classList.add("hide");
                    contentBoxShare.classList.add("hide");
                    contentBoxShare.classList.replace("contentIn", "contentOut");
                }, 300);
            }
        });
    }

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    const initialPage = {
        opacity: 0
    }
    const whileInViewPage = {
        opacity: 1
    }
    const transitionPage = {
        duration: 0.5
    }
    const viewportPage = {
        once: true
    }

    return (
        <motion.div initial={initialPage} whileInView={whileInViewPage} transition={transitionPage} viewport={viewportPage} className='main'>
            <PopShare />
            <OverlaySearch />
            <TopBar />
            <HeaderPages />
            <div className="page-content-article d-flex flex-v g50">

                <div className="c100 d-flex flex-v-m ai-fs g30 mb-50">

                    {
                        isLoading ? (
                            <LoaderComponants />
                        ) : (
                            <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="c70 c100m d-flex flex-v g10">

                                <div className="c100 d-flex ai-c jc-sb g30">
                                    <div className="title">Title Article 1</div>
                                    <div id="btnShare" className='btn btn-primary-border fs-14' onClick={openPopShare}><i className="bi bi-share-fill"></i> Share</div>
                                </div>

                                <div className="d-flex ai-c g5 mb-30">
                                    <div className="date">Sunday February 21, 2021</div>
                                    <span></span>
                                    <div className="author">by John Doe</div>
                                </div>

                                <LazyLoadImage className='img-article' width="100%" height="500px" src={article1} effect='blur' alt="" />

                                <div className="content-article d-flex flex-v g10 mt-30">
                                    <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Eos, officiis?</p>
                                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Obcaecati suscipit accusantium, asperiores tenetur repudiandae nulla quas corrupti reiciendis temporibus sit, inventore optio, dignissimos unde provident quod. Laboriosam id eligendi dolores harum dolor officia asperiores laborum tenetur enim. Odio quasi, maiores commodi quis culpa explicabo mollitia ipsam similique? Nisi neque natus necessitatibus earum eius veritatis minima perferendis rem repudiandae a labore, reprehenderit nobis minus suscipit veniam? Iure, illum. Sint vel quos consequatur, est exercitationem numquam animi quia quas aliquam? Minima quam animi optio aliquam unde impedit modi iusto eveniet quia consectetur, repellat omnis suscipit beatae veniam quas accusantium reiciendis. Laborum, reprehenderit.</p>
                                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Quibusdam, inventore vel facilis voluptates numquam culpa modi minima? Dolores tempora quis enim earum aperiam dolor illo repellat laborum dolorum maxime sint ullam eligendi blanditiis placeat neque quae, magnam, alias beatae esse fuga nemo quaerat? Harum dolore aliquam enim a quisquam blanditiis!</p>
                                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nulla facilis illum officiis aspernatur quod hic facere impedit animi adipisci officia.</p>
                                </div>

                                <div className="c100 d-flex ai-c jc-sb mt-50">
                                    <div className='btn btn-primary-border fs-14'><i className="bi bi-arrow-left-circle"></i> Previous article</div>
                                    <div className='btn btn-primary-border fs-14'>Next article<i className="bi bi-arrow-right-circle"></i></div>
                                </div>

                            </motion.div>
                        )
                    }
                    {
                        isLoading ? (
                            <LoaderComponants />
                        ) : (
                            <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="colomn-right c30 c100m d-flex flex-v g30">

                                <div className="title">Last Post</div>

                                <div className="card-article-colomn-right c100 d-flex flex-v">

                                    <LazyLoadImage width="100%" height="200px" src={article2} effect='blur' alt="" />

                                    <div className='c100 p20 d-flex flex-v g10'>
                                        <div className="title">Title article 2</div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Rerum, facilis...
                                        </div>
                                        <div className="btn btn-primary w-fc">Read</div>
                                    </div>
                                </div>
                                <div className="card-article-colomn-right c100 d-flex flex-v">
                                    <LazyLoadImage width="100%" height="200px" src={article3} effect='blur' alt="" />
                                    <div className='c100 p20 d-flex flex-v g10'>
                                        <div className="title">Title article 3</div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Rerum, facilis...
                                        </div>
                                        <div className="btn btn-primary w-fc">Read</div>
                                    </div>
                                </div>
                                <div className="card-article-colomn-right c100 d-flex flex-v">
                                    <LazyLoadImage width="100%" height="200px" src={article4} effect='blur' alt="" />
                                    <div className='c100 p20 d-flex flex-v g10'>
                                        <div className="title">Title article 4</div>
                                        <div className="short-description">
                                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Rerum, facilis...
                                        </div>
                                        <div className="btn btn-primary w-fc">Read</div>
                                    </div>
                                </div>

                            </motion.div>
                        )
                    }



                </div>

                <BlocNewsletterSub />
            </div>
            <Footer />
            <Copyright />
        </motion.div>
    );
};

export default Articles;