import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import LoaderComponants from './LoaderComponants';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { motion } from "framer-motion"

const ListByIngredient = () => {

    const [isLoading, setIsLoading] = useState(true);
    const currentURL = window.location.href;
    const isHomePage = currentURL.endsWith('/');

    const numberDisplayIngredient = isHomePage ? '30' : '1000';
    const displayBtnAllIngredient = isHomePage ? '' : 'hide';

    const [mealsIngredientsData, setMealsIngredientsData] = useState([]);

    const fetchData = () => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/list.php?i=list")
            .then((res) => {
                setMealsIngredientsData(res.data.meals);
                setIsLoading(false);
            })
            .catch((error) => {
                console.error("Error fetching data:", error);
                setIsLoading(false);
            });
    }

    useEffect(() => {
        fetchData();
    }, []);

    const refreshData = () => {
        fetchData();
    }

    // Fonction pour mlanger le tableau d'ingrdients de manire alatoire
    const shuffleIngredients = (array) => {
        let currentIndex = array.length, randomIndex;
        while (currentIndex !== 0) {
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex--;
            [array[currentIndex], array[randomIndex]] = [
                array[randomIndex],
                array[currentIndex],
            ];
        }
        return array;
    };

    // Mlangement du tableau d'ingrdients avant l'affichage
    const shuffledIngredients = shuffleIngredients([...mealsIngredientsData]);

    const handleNavLinkClick = () => {
        setTimeout(() => {
            window.scrollTo({
                top: 0,
                behavior: "smooth"
            });
        }, 500);
    }

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true
    }

    return (
        <div className="list-by-ingredient c100">

            {
                isLoading ? (
                    <LoaderComponants />
                ) : (

                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport}>

                        <div className="c100m d-flex flex-v-m g10m ai-c jc-sb mb-20">
                            <div className='title'>Recipe by ingredient</div>

                            <div className="d-flex ai-c g20">
                                <div className="btn btn-gray-border d-flex ai-c g5" onClick={refreshData}>
                                    <i className="bi bi-arrow-clockwise fs-18"></i>
                                    refresh list
                                </div>
                                <NavLink className={`btn btn-primary-border fs-14 ${displayBtnAllIngredient}`} to="/search-by-ingredient">
                                    See all ingredients <i className="bi bi-arrow-right-circle fs-18"></i>
                                </NavLink>
                            </div>

                        </div>

                        <div className='container d-flex jc-sb jc-fs-m wrap'>
                            {shuffledIngredients
                                .slice(0, numberDisplayIngredient)
                                .map((mealsIngredients) => {
                                    const ingredientName = mealsIngredients.strIngredient;
                                    const formattedIngredient = ingredientName.replace(/\s/g, "-");
                                    return (
                                        <NavLink
                                            key={mealsIngredients.idIngredient}
                                            to={`/search-by-ingredient/${formattedIngredient}`}
                                            onClick={handleNavLinkClick}>

                                            <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className='box-ingredient d-flex flex-v ai-c g10'>
                                                <LazyLoadImage key={mealsIngredients.strIngredient} width="50" height="50" src={"https://www.themealdb.com/images/ingredients/" + mealsIngredients.strIngredient + ".png"} effect='blur' alt={'photo de ' + mealsIngredients.strIngredient} />
                                                <span>{mealsIngredients.strIngredient}</span>
                                            </motion.div>

                                        </NavLink>
                                    );
                                })
                            }
                        </div>

                    </motion.div>

                )
            }

        </div>
    );
};

export default ListByIngredient;
