import React from 'react';

const PopLogin = () => {

    const BtnClosePopLogin = () => {
        const boxLogin = document.querySelector(".box-login");
        const contentBoxLogin = document.querySelector(".content-box-login");
        boxLogin.classList.replace("bgIn", "bgOut");
        setTimeout(() => {
            boxLogin.classList.add("hide");
            contentBoxLogin.classList.add("hide");
            contentBoxLogin.classList.replace("contentIn", "contentOut");
        }, 300);
    }

    return (
        <div className='box-login bgOut hide'>
            <div className="content-box-login box d-flex flex-v ai-c g20 contentOut hide">
                <div className="btn-close-box-login" onClick={BtnClosePopLogin}><i className="bi bi-x-lg"></i></div>

                <div className="c100 d-flex flex-v ai-c g10">
                    <div className="title">Login</div>
                    <div className="sub-title">Enter your account information to login</div>
                </div>

                {/* <div className="c100 p15 br10 warning-message"><i className="bi bi-exclamation-triangle-fill"></i> This form is not functional</div> */}

                <div className="c100 d-flex flex-v g5">
                    <label htmlFor="email">Email adress</label>
                    <input type="email" name='email' className='c100' placeholder='Your email adress...' />
                </div>
                <div className="c100 d-flex flex-v g5">
                    <label htmlFor="password">Password</label>
                    <input type="password" name='password' className='c100' placeholder='Your password...' />
                </div>
                <div className="c100">
                    <button type='submit' className='btn btn-primary c100 d-flex jc-c pv-10'>Login</button>
                </div>

                <div className="c100 bb-1 fc-black10"></div>

                <div className="sub-title">Or create an account</div>

                <div className="c100">
                    <button type='submit' className='btn btn-primary-border c100 d-flex jc-c pv-10'>Sign In</button>
                </div>


            </div>
        </div>
    );
};

export default PopLogin;