import axios from 'axios';
import React, { useEffect, useState } from 'react';
import CardResult from './CardResult';

const SearchForm = () => {

    const [mealsData, setMealsData] = useState([]);
    const [inputSearch, setInputSearch] = useState("");
    const [showResults, setShowResults] = useState(false);
    const [upFormSearch, setUpFormSearch] = useState(false);

    useEffect(() => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/search.php?s=" + inputSearch)
            .then((res) => setMealsData(res.data.meals));
    }, [inputSearch]);

    const handleInputChange = (e) => {
        setInputSearch(e.target.value);
        setShowResults(true);

        const instantResult = document.querySelector(".instant-result");


        window.addEventListener("click", (event) => {
            if (!instantResult.contains(event.target)) {
                setShowResults(false);
            }
        });
    }

    const inputSearchHtml = document.querySelector("#inputSearch");
    const closeCardResult = () => {
        inputSearchHtml.value = '';
        setShowResults(false);
    }

    return (
        <div className={`search-form c100m pos-r`}>
            <input type="text" id="inputSearch" className='c100m' placeholder='Search your favorite dish... (english only)' onChange={handleInputChange} />
            <div className={`instant-result c100m bg-white br20 ${showResults ? "" : "hide"}`}>
                <div className='box-instant-result d-flex flex-v' onClick={closeCardResult}>
                    {
                        mealsData && mealsData
                            .slice(0, 24)
                            .map((meal) => <CardResult key={meal.idMeal} meal={meal} />)
                    }
                </div>
            </div>
        </div>
    );
};

export default SearchForm;