import React from 'react';
import TopBar from '../components/TopBar';
import Header from '../components/Header';
import ListByCategory from '../components/ListByCategory';
import ListByIngredient from '../components/ListByIngredient';
import ListByCountry from '../components/ListByCountry';
import RecipeRandom from '../components/RecipeRandom';
import Footer from '../components/Footer';
import OverlaySearch from '../components/OverlaySearch';
import ListBlog from '../components/ListBlog';
import BlocNewsletterSub from '../components/BlocNewsletterSub';
import Copyright from '../components/Copyright';
import { motion } from "framer-motion"

const Home = () => {

    const initialPage = {
        opacity: 0
    }
    const whileInViewPage = {
        opacity: 1
    }
    const transitionPage = {
        duration: 0.5
    }
    const viewportPage = {
        once: true
    }

    return (

        <motion.div initial={initialPage} whileInView={whileInViewPage} transition={transitionPage} viewport={viewportPage} className="main">
            <OverlaySearch />
            <TopBar />
            <Header />
            <div className="page-content c100m d-flex flex-v">
                <RecipeRandom />
                <ListByCountry />
                <ListByCategory />
                <ListByIngredient />
                <ListBlog />
                <BlocNewsletterSub />
            </div>
            <Footer />
            <Copyright />
        </motion.div>

    );
};

export default Home;