import React from 'react';
import SearchForm from './SearchForm';
import videoBg from "../assets/img/bg-video.mp4";

const Header = () => {

    return (
        <div className='header'>
            <video src={videoBg} autoPlay loop muted></video>
            <div className="content-header d-flex jc-c ai-c">

                <div className="box-search-form d-flex flex-v ai-c">
                    <div className="d-flex flex-v ai-c mb-20">
                        <span className='title'>Find your favorite dish</span>
                        <span className='sub-title'>from over +3,000 recipes</span>
                    </div>
                    <SearchForm />
                </div>

            </div>
        </div>
    );
};

export default Header;
