import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import ListByIngredient from '../components/ListByIngredient';
import TopBar from '../components/TopBar';
import Card from '../components/Card';
import HeaderPages from '../components/HeaderPages';
import OverlaySearch from '../components/OverlaySearch';
import Footer from '../components/Footer';
import Copyright from '../components/Copyright';
import { motion } from "framer-motion"

const SearchByIngredient = () => {

    const location = useLocation();
    const cleanIngredient = location.pathname.split('/').pop();
    const deformattedIngredient = cleanIngredient.replace(/-/g, " ");
    const [mealsIngredientsData, setMealsIngredientsData] = useState([]);
    const [displayErrorMessage, setDisplayErrorMessage] = useState(false);

    const fetchData = () => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/filter.php?i=" + deformattedIngredient)
            .then((res) => {
                if (res.data.meals !== null) {
                    setMealsIngredientsData(res.data.meals);
                } else {
                    setMealsIngredientsData([]); // Dfinir comme un tableau vide si pas de rsultats
                }
            })
            .catch((error) => {
                console.error("Erreur lors de la rcupration des donnes :", error);
                setMealsIngredientsData([]); // Dfinir comme un tableau vide en cas d'erreur
            });
    };

    useEffect(() => {
        setTimeout(() => { fetchData(); }, 50);
    }, [cleanIngredient]);

    useEffect(() => {
        if (cleanIngredient === 'search-by-ingredient') {
            setDisplayErrorMessage(true);
        } else {
            setDisplayErrorMessage(false);
        }
    }, []);

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    const initialPage = {
        opacity: 0
    }
    const whileInViewPage = {
        opacity: 1
    }
    const transitionPage = {
        duration: 0.5
    }
    const viewportPage = {
        once: true
    }

    return (
        <motion.div initial={initialPage} whileInView={whileInViewPage} transition={transitionPage} viewport={viewportPage} className="main">
            <OverlaySearch />
            <TopBar />
            <HeaderPages />
            <div className="page-content-list-ingredient">
                <div className='d-flex flex-v g50 mt-50'>

                    {mealsIngredientsData.length === 0 ? (

                        <div className={`c100 p30 d-flex flex-v ai-c g20 ${displayErrorMessage ? 'hide' : ''}`}>
                            <i className="bi bi-x-lg fs-40 fc-black20"></i>
                            <div className='fc-black30 fs-22 fw-600'>No results correspond to "{cleanIngredient}"</div>
                            <div className='fc-black30 fs-18 fw-600'>Why not choose another ?</div>
                        </div>

                    ) : (

                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="d-flex flex-v g30">

                            <div className="c100 d-flex jc-fs jc-c-m">
                                <div className='fs-28 fs-22m fw-600 '>All recipes with {cleanIngredient}</div>
                            </div>

                            <div className="d-flex g30 g0m wrap">
                                {mealsIngredientsData.map((meal) => <Card key={meal.idMeal} meal={meal} />)}
                            </div>
                        </motion.div>

                    )}

                    <ListByIngredient />

                </div>
            </div>
            <Footer />
            <Copyright />
        </motion.div >
    );
};

export default SearchByIngredient;