import React from 'react';
import { motion } from "framer-motion"

const BlocNewsletterSub = () => {

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }

    return (
        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className='bloc-newsletter-sub d-flex flex-v ai-c'>
            <div className="title ta-c-m">Be the first to know about new recipes </div>
            <div className="sub-title ta-c-m">Be the first to know about new recipes </div>

            <div className="c100m d-flex flex-v-m ai-c g20 mt-20">
                <input type="text" placeholder='Enter your email address...' className='input-newsletter-sub' />
                <button type='submit' className='button-newsletter-sub'>Subscribe</button>
            </div>

        </motion.div>
    );
};

export default BlocNewsletterSub;