import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { NavLink, useLocation } from 'react-router-dom';
import OverlaySearch from '../components/OverlaySearch';
import TopBar from '../components/TopBar';
import HeaderPages from '../components/HeaderPages';
import Footer from '../components/Footer';
import PopShare from '../components/PopShare';
import Comments from '../components/Comments';
import LoaderComponants from '../components/LoaderComponants';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import Copyright from '../components/Copyright';
import { motion } from "framer-motion"

const Recipe = () => {

    const [isLoading, setIsLoading] = useState(true);
    const location = useLocation();
    const cleanUrlId = location.pathname.split('/').pop();

    const [mealIdData, setMealIdData] = useState([]);
    const [checkedIngredients, setCheckedIngredients] = useState([]);
    const [isFavorite, setIsFavorite] = useState(false);

    const [showVideo, setShowVideo] = useState(false);
    const linkVideo = mealIdData[0]?.strYoutube.replace("https://www.youtube.com/", "https://www.youtube.com/embed/").replace("watch?v=", "");

    const fetchData = () => {
        axios
            .get("https://www.themealdb.com/api/json/v1/1/lookup.php?i=" + cleanUrlId)
            .then((res) => {
                setMealIdData(res.data.meals);
                setIsLoading(false);
            })
            .catch((error) => {
                console.error("Error fetching data:", error);
                setIsLoading(false);
            });
    }

    useEffect(() => {
        fetchData();
    }, [cleanUrlId]);


    const handleCheckboxChange = (event) => {
        const ingredientId = event.target.id;
        const ingredientElement = document.querySelector(`label[for="${ingredientId}"] .c70`);
        if (event.target.checked) {
            ingredientElement.style.textDecoration = 'line-through';
            setCheckedIngredients([...checkedIngredients, ingredientId]);
        } else {
            ingredientElement.style.textDecoration = 'none';
            setCheckedIngredients(checkedIngredients.filter((id) => id !== ingredientId));
        }
    };

    const openPopShare = () => {
        const boxShare = document.querySelector(".box-share");
        const contentBoxShare = document.querySelector(".content-box-share");
        boxShare.classList.remove("hide");
        boxShare.classList.replace("bgOut", "bgIn");
        setTimeout(() => {
            contentBoxShare.classList.remove("hide");
            contentBoxShare.classList.replace("contentOut", "contentIn");
        }, 300);
        closePopShare();
    }

    const closePopShare = () => {
        const boxShare = document.querySelector(".box-share");
        const contentBoxShare = document.querySelector(".content-box-share");
        boxShare.addEventListener("click", (event) => {
            if (!contentBoxShare.contains(event.target)) {
                boxShare.classList.replace("bgIn", "bgOut");
                setTimeout(() => {
                    boxShare.classList.add("hide");
                    contentBoxShare.classList.add("hide");
                    contentBoxShare.classList.replace("contentIn", "contentOut");
                }, 300);
            }
        });
    }

    useEffect(() => {
        checkIfRecipeIsFavorite();
    }, [mealIdData]);

    const checkIfRecipeIsFavorite = () => {

        const recipe = mealIdData[0];

        if (recipe) {
            const { idMeal } = recipe;

            if (typeof Storage !== "undefined") {
                const existingFavorites = JSON.parse(localStorage.getItem("EasyCookingFavoriteRecipe")) || [];
                setIsFavorite(false);

                // Vrifier si la recette est dj dans les favoris
                if (existingFavorites.some((favorite) => favorite.idMeal === idMeal)) {
                    //console.log("Cette recette est dj dans vos favoris.");
                    setIsFavorite(true);
                }
            } else {
                //console.log("Dsol, votre navigateur ne supporte pas le Web Storage.");
                setIsFavorite(false);
            }
        } else {
            //console.log("Cette recette n'est pas dans vos favoris.");
            setIsFavorite(false);
        }
    };

    const addToFavorite = () => {

        const recipe = mealIdData[0];

        if (recipe) {
            const { idMeal } = recipe;

            if (typeof Storage !== "undefined") {
                const existingFavorites = JSON.parse(localStorage.getItem("EasyCookingFavoriteRecipe")) || [];

                // Vrifier si la recette est dj dans les favoris
                if (existingFavorites.some((favorite) => favorite.idMeal === idMeal)) {
                    console.log("Cette recette est dj dans vos favoris.");
                } else {
                    // Ajouter la nouvelle recette  la liste des favoris
                    const newFavorite = { idMeal, recipeTitle: recipe.strMeal };
                    existingFavorites.push(newFavorite);

                    // Sauvegarder la liste mise  jour dans le localStorage
                    localStorage.setItem("EasyCookingFavoriteRecipe", JSON.stringify(existingFavorites));

                    setIsFavorite(true);
                }
            } else {
                //console.log("Dsol, votre navigateur ne supporte pas le Web Storage.");
                setIsFavorite(false);
            }
        } else {
            //console.log("Donnes de recette non disponibles.");
            setIsFavorite(false);
        }
    };

    const removeFromFavorite = () => {
        const recipe = mealIdData[0];
        if (recipe) {
            const { idMeal } = recipe;
            const existingFavorites = JSON.parse(localStorage.getItem("EasyCookingFavoriteRecipe")) || [];
            const updatedFavorites = existingFavorites.filter((favorite) => favorite.idMeal !== idMeal);
            localStorage.setItem("EasyCookingFavoriteRecipe", JSON.stringify(updatedFavorites));
            setIsFavorite(false); // Mettre  jour l'tat pour afficher le bouton "Ajouter aux favoris"
        } else {
            console.log("Donnes de recette non disponibles.");
        }
    };

    const recipePrint = () => {
        window.print();
    }

    const toggleShowVideo = () => {
        setShowVideo(true);
    }

    const initial = {
        y: 50,
        opacity: 0
    }
    const whileInView = {
        y: 0,
        opacity: 1
    }
    const transition = {
        type: "spring",
        stiffness: 260,
        damping: 20
    }
    const viewport = {
        once: true,
        amount: 0.1
    }


    return (
        <div className='main'>
            <PopShare />
            <OverlaySearch />
            <TopBar />
            <HeaderPages />

            <div className="page-content-recipe d-flex flex-v g50">

                {
                    isLoading ? (
                        <LoaderComponants />
                    ) : (
                        <div>

                            {mealIdData && mealIdData
                                .map((recipe) => (

                                    <div key={recipe.idMeal} className="page-recipe d-flex flex-v g50">

                                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="header-recipe c100 d-flex flex-v ai-c jc-sb">

                                            <div className="c100 d-flex flex-v-m ai-c jc-sb jc-c-m g20m mb-10">
                                                <div className="c70 c100m d-flex-flex-v g10">
                                                    <div className='title ta-c-m' key={cleanUrlId}>{recipe.strMeal}</div>
                                                    <div className="tag-container-recipe d-flex jc-c-m ai-c g10 wrap">
                                                        <NavLink to={`/search-by-country/${recipe.strArea}`}>
                                                            <div className="d-flex ai-c g5">
                                                                <i className="bi bi-geo-alt-fill"></i>
                                                                <span>{recipe.strArea}</span>
                                                            </div>
                                                        </NavLink>
                                                        
                                                        <NavLink to={`/search-by-categorie/${recipe.strCategory}`}>
                                                            <div className="d-flex ai-c g5">
                                                                <i className="bi bi-list-ul"></i>
                                                                <span>{recipe.strCategory}</span>
                                                            </div>
                                                        </NavLink>
                                                        
                                                        <div className="d-flex ai-c g10">
                                                            Tags :
                                                            {recipe.strTags && recipe.strTags
                                                                .split(',')
                                                                .map((tags, index) => (
                                                                    <div key={index} className='tag'>{tags}</div>
                                                                ))
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="c30 c100m d-flex ai-c jc-fe jc-c-m g20">
                                                    <div id="btnShare" className='btn btn-primary-border fs-14' onClick={openPopShare}><i className="bi bi-share-fill"></i> Share</div>
                                                    {
                                                        isFavorite ? (
                                                            <div id="btnDeleteFavorite" className='btn btn-red fs-14' onClick={removeFromFavorite}><i className="bi bi-heart-fill"></i> Delete favorite</div>
                                                        ) : (
                                                            <div id="btnAddFavorite" className='btn btn-red-border fs-14' onClick={addToFavorite}><i className="bi bi-heart"></i> Add my favorite</div>
                                                        )
                                                    }
                                                </div>
                                            </div>

                                            <div className="info-recipe c100 d-flex flex-v-m g10m jc-sb ai-c">

                                                <div className="container-info-recipe d-flex ai-c g30">
                                                    <div className="d-flex ai-c g5">
                                                        <i className="bi bi-stopwatch-fill"></i>
                                                        <span>20 min.</span>
                                                    </div>
                                                    <div className="d-flex ai-c g5">
                                                        <i className="bi bi-bookmark-star-fill"></i>
                                                        <span>Very easy</span>
                                                    </div>
                                                    <div className="d-flex ai-c g5">
                                                        <i className="bi bi-basket3-fill"></i>
                                                        <span>Cheap</span>
                                                    </div>
                                                </div>

                                                <div className="container-rating-recipe d-flex ai-c g10">
                                                    <div className="star-recipe d-flex ai-c">
                                                        <i className="bi bi-star-fill"></i>
                                                        <i className="bi bi-star-fill"></i>
                                                        <i className="bi bi-star-fill"></i>
                                                        <i className="bi bi-star-fill"></i>
                                                        <i className="bi bi-star-half"></i>
                                                    </div>
                                                    <span className='rating-recipe'>4.5/5</span>
                                                    <a href="#comments" className='nbr-comment-recipe'>(4 comments)</a>
                                                </div>

                                            </div>

                                        </motion.div>

                                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="content-recipe d-flex flex-v-m ai-fs g30">

                                            <div className="c50 c100m d-flex flex-v g20">

                                                {
                                                    showVideo ? (
                                                        <iframe width="100%" height="450px" src={linkVideo} title={recipe.strMeal} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen></iframe>
                                                    ) : (
                                                        <div className="toggle-video pos-r c100" onClick={toggleShowVideo}>
                                                            <div className="overlay-play c100 h100p d-flex jc-c ai-c">
                                                                <i className="bi bi-play-circle"></i>
                                                            </div>

                                                            <LazyLoadImage key={recipe.strMealThumb} width="100%" height="500px" src={recipe.strMealThumb} effect='blur' alt={'photo de ' + recipe.strMeal} />

                                                        </div>
                                                    )
                                                }

                                            </div>

                                            <div className="c50 c100m d-flex flex-v g30">

                                                <div className="c100 d-flex-flex-v g10">

                                                    <div className='sub-title'>Ingredients</div>
                                                    <div className="list-ingredient d-flex flex-v pv-10">
                                                        {
                                                            Array.from({ length: 30 }, (_, i) => i + 1).map((index) => (
                                                                recipe[`strIngredient${index}`] && (
                                                                    <div className="ingredient c100 d-flex ai-c jc-sb" key={index}>
                                                                        <div className="c5 d-flex jc-c pr-10">
                                                                            <input type="checkbox" style={{ display: 'none' }} id={recipe[`strIngredient${index}`]} onChange={handleCheckboxChange} />
                                                                            {checkedIngredients.includes(recipe[`strIngredient${index}`]) ? (
                                                                                <i className="bi bi-check-circle-fill fc-green fs-18"></i>
                                                                            ) : (
                                                                                <i className="bi bi-circle fs-18"></i>
                                                                            )}
                                                                        </div>
                                                                        <label className='c100 d-flex ai-c' htmlFor={recipe[`strIngredient${index}`]}>
                                                                            <div className={`c70 cur-p ${checkedIngredients.includes(recipe[`strIngredient${index}`]) ? 'strikethrough fc-green' : ''}`}>
                                                                                {recipe[`strIngredient${index}`]}
                                                                            </div>
                                                                            <div className="c30 cur-p ta-r">{recipe[`strMeasure${index}`]}</div>
                                                                        </label>
                                                                    </div>
                                                                )
                                                            ))
                                                        }
                                                    </div>
                                                </div>

                                            </div>

                                        </motion.div>


                                        <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} className="instruction-recipe c100">

                                            <div className="header-instruction-recipe c100 d-flex ai-c jc-sb">
                                                <div className='sub-title'>Instructions</div>
                                                <a href="" className='btn btn-primary-border fs-13' onClick={recipePrint}><i className="bi bi-printer-fill"></i> Print recipe</a>
                                            </div>

                                            {recipe.strInstructions && recipe.strInstructions
                                                .split('. ')
                                                .map((instruction, index) => (

                                                    <motion.div initial={initial} whileInView={whileInView} transition={transition} viewport={viewport} key={index} className="step-instruction-recipe d-flex ai-c jc-fs">

                                                        <div className="step-number-instruction-recipe d-flex flex-v jc-c ai-c">
                                                            <span>Step</span>
                                                            <span>{index + 1}</span>
                                                        </div>
                                                        <div className="text-instruction-recipe">
                                                            {instruction}
                                                        </div>

                                                    </motion.div>

                                                ))}
                                        </motion.div>

                                    </div>

                                ))
                            }

                        </div>
                    )
                }

                <div id="comments" className="c100 d-flex flex-v">
                    <Comments />
                </div>

            </div>

            <Footer />
            <Copyright />
        </div>
    );
};

export default Recipe;