import React, { useEffect, useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from './pages/Home';
import About from './pages/About';
import NotFound from './pages/NotFound';
import SearchByCategorie from './pages/SearchByCategorie';
import SearchByIngredient from './pages/SearchByIngredient';
import SearchByCountry from './pages/SearchByCountry';
import Recipe from './pages/Recipe';
import Blog from './pages/Blog';
import Articles from './pages/Articles';
import MyFavorites from './pages/MyFavorites';
import PreLoader from './components/PreLoader';

const App = () => {

  const [siteIsLoading, setSiteIsLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setSiteIsLoading(false);
    }, 5000);
  });

  return (
    <div>
      {siteIsLoading ? <PreLoader /> : null}
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/search-by-categorie" element={<SearchByCategorie />} />
          <Route path="/search-by-categorie/:category" element={<SearchByCategorie />} />
          <Route path="/search-by-ingredient" element={<SearchByIngredient />} />
          <Route path="/search-by-ingredient/:ingredient" element={<SearchByIngredient />} />
          <Route path="/search-by-country" element={<SearchByCountry />} />
          <Route path="/search-by-country/:country" element={<SearchByCountry />} />
          <Route path="/recipe" element={<Recipe />} />
          <Route path="/recipe/:name" element={<Recipe />} />
          <Route path="/about" element={<About />} />
          <Route path="/blog" element={<Blog />} />
          <Route path="/blog/article" element={<Articles />} />
          <Route path="/myfavoriteList" element={<MyFavorites />} />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
};

export default App;