import React, { useEffect } from 'react';

const PopShare = () => {

    useEffect(() => {

        const inputLinkPage = document.querySelector("#input-link-page");
        const currentUrl = window.location.href;

        inputLinkPage.value = currentUrl;

        const btnCopyUrl = document.querySelector(".btn-copy-url");
        btnCopyUrl.addEventListener("click", () => copyUrl(inputLinkPage));

    }, []);

    const copyUrl = (inputLinkPage) => {
        inputLinkPage.select();
        document.execCommand("copy");
        const messageShare = document.querySelector(".message-share");
        messageShare.classList.remove("hide");
        setTimeout(() => { messageShare.classList.add("hide"); }, 5000);
    }

    const btnClosePopShare = () => {
        const boxShare = document.querySelector(".box-share");
        const contentBoxShare = document.querySelector(".content-box-share");
        boxShare.classList.replace("bgIn", "bgOut");
        setTimeout(() => {
            boxShare.classList.add("hide");
            contentBoxShare.classList.add("hide");
            contentBoxShare.classList.replace("contentIn", "contentOut");
        }, 300);
    }

    return (
        <div className='box-share bgOut hide'>
            <div className="content-box-share box d-flex flex-v ai-c g20 contentOut hide">
                <div className="btn-close-box-share" onClick={btnClosePopShare}><i className="bi bi-x-lg"></i></div>

                <div className="title">Share this recipe</div>
                <div className="sub-title">Copy this link and paste it wherever you like</div>

                <div className="c100 d-flex flex-v ai-c g5">
                    <div className="c100 d-flex ai-c jc-sb bg-black10 br10 p10">
                        <input type="text" id="input-link-page" />
                        <div className="btn-copy-url">
                            <i className="bi bi-subtract"></i>
                        </div>
                    </div>
                    <div className="message-share c100 ta-c fc-green fs-14 hide">Copied to clipboard !</div>
                </div>

                <div className="c100 bb-1 fc-black10"></div>

                <div className="sub-title">Or, share this recipe directly on your social networks</div>

                <div className="icons-rs-share d-flex ai-c jc-c g30">
                    <a href={`https://www.facebook.com/sharer/sharer.php?u=${window.location.href}`} target='_blank'><i className="bi bi-facebook"></i></a>
                    <a href={`https://twitter.com/intent/tweet?url=${window.location.href}&text=This app is amazing !`} target='_blank'><i className="bi bi-twitter"></i></a>
                    <a href="https://www.instagram.com" target='_blank'><i className="bi bi-instagram"></i></a>
                    <a href={`mailto:adresse_email@example.com?subject=Sujet%20du%20message&body=Look%20at%20this%20!%20:%20${window.location.href}`}><i className="bi bi-envelope"></i></a>
                </div>

            </div>
        </div>
    );
};

export default PopShare;