import React from 'react';
import Logo from '../assets/img/logo.png'
import { NavLink } from 'react-router-dom';

const BlocMenuMobile = () => {

    const closeMenuMobile = () => {
        const menuMobile = document.querySelector(".menu-mobile");
        const contentMenuMobile = document.querySelector(".content-menu-mobile");
        contentMenuMobile.classList.replace("bgIn", "bgOut");
        setTimeout(() => {
            menuMobile.style.width = '0';
        }, 150);
    }

    return (
        <div className={`menu-mobile`}>
            <div className="content-menu-mobile d-flex flex-v ai-c jc-c g30 bgOut">
                <div className="btn-close-menu-mobile" onClick={closeMenuMobile}><i className="bi bi-x-lg"></i></div>
                <img src={Logo} alt="" />
                <div className="c70 bb-1 fc-white20"></div>
                <div className="c100 d-flex flex-v ai-c g20">
                    <NavLink to="/myfavoritelist">
                        <span>My Favorites recipes</span>
                    </NavLink>
                    <div className="c70 bb-1 fc-white20 mv-10"></div>
                    <NavLink to="/">
                        <span>Home</span>
                    </NavLink>
                    <NavLink to="/search-by-categorie">
                        <span>Search by category</span>
                    </NavLink>
                    <NavLink to="/search-by-country">
                        <span>Search by country</span>
                    </NavLink>
                    <NavLink to="/search-by-ingredient">
                        <span>Search by ingredient</span>
                    </NavLink>
                    <NavLink to="/blog">
                        <span>Blog</span>
                    </NavLink>
                    <NavLink to="/about">
                        <span>About this app</span>
                    </NavLink>
                </div>
            </div>
        </div>
    );
};

export default BlocMenuMobile;